import React from "react";
import { Star, Joystick } from "lucide-react";
import "./Arcade.css";
import { motion } from 'framer-motion';

const Flicker = ({ children }) => (
  <motion.div
    animate={{
      opacity: [1, 0.7, 0.9, 0.6, 1],
    }}
    transition={{
      duration: 2,
      repeat: Infinity,
      repeatType: "loop",
      ease: "easeInOut",
      repeatDelay: Math.random() * 1.5 + 0.5
    }}
    style={{
        filter: `drop-shadow(0 0 8px currentColor)`
    }}
  >
    {children}
  </motion.div>
);

export default function ArcadeShell({ children, helpDisplay, right }) {
  return (
    <div className="flex items-start justify-center p-4">
      <div className="arcade-machine">
        <div className="arcade-top">
          <div className="arcade-title-marquee">
            <div className="scanline-overlay"></div>
            <div className="flex items-center gap-2" style={{ color: 'var(--glow-color)'}}>
              <Flicker><Joystick className="w-6 h-6" /></Flicker>
              <h1 className="arcade-title-text">Starcade</h1>
            </div>
            <div className="arcade-help-wrapper">{helpDisplay}</div>
            <div className="flex items-center gap-2" style={{ color: 'var(--glow-color)'}}>
              <Flicker><Star className="w-6 h-6" /></Flicker>
              {right}
            </div>
          </div>
        </div>

        <motion.div
          layout
          transition={{ duration: 0.5, type: 'spring', bounce: 0.2 }}
          className="arcade-body"
          style={{ overflow: 'hidden' }}
        >
          <div style={{ display: 'contents' }}>
            {children}
          </div>
        </motion.div>

        <div className="gameboy-controls">
          <div className="dpad">
            <div className="dpad-button dpad-up"></div>
            <div className="dpad-button dpad-left"></div>
            <div className="dpad-button dpad-center"></div>
            <div className="dpad-button dpad-right"></div>
            <div className="dpad-button dpad-down"></div>
          </div>

          <div className="action-cross-buttons">
            <button className="button y">Y</button>
            <button className="button x">X</button>
            <button className="button b">B</button>
            <button className="button a">A</button>
          </div>

          <div className="menu-buttons">
            <button className="start">START</button>
            <button className="select">SELECT</button>
          </div>

          <div className="speaker-grill"></div>
        </div>
      </div>
    </div>
  );
}
